import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject } from 'rxjs';
import '../../models/product';

import { baseUrl } from '../../config/variables';

@Injectable({
  providedIn: 'root',
})
export class ProductsService {
  constructor(private http: HttpClient) {}

  products = new BehaviorSubject<[Product]>(null);

  getAll() {
    return this.http.get(baseUrl + '/product').subscribe((res) => {
      if (res['data']) {
        this.products.next(res['data']);
      }
    });
  }

  getOne(itemId) {
    return this.http.get(baseUrl + `/product/${itemId}`);
  }

  addOne(form) {
    return this.http.post(baseUrl + '/product', form);
  }

  updateOne(form, itemId) {
    return this.http.put(baseUrl + `/product/${itemId}`, form);
  }

  deleteOne(itemId) {
    return this.http.delete(baseUrl + `/product/${itemId}`);
  }

  activateOne(itemId) {
    return this.http.get(baseUrl + `/product/activate/${itemId}`);
  }

  deactivateOne(itemId) {
    return this.http.get(baseUrl + `/product/deactivate/${itemId}`);
  }

  soldOne(itemId) {
    return this.http.get(baseUrl + `/product/sold/${itemId}`);
  }

  availableOne(itemId) {
    return this.http.get(baseUrl + `/product/available/${itemId}`);
  }

  addDiscount(form, itemId) {
    return this.http.post(baseUrl + `/product/addDiscount/${itemId}`, form);
  }

  deleteDiscount(itemId) {
    return this.http.delete(baseUrl + `/product/deleteDiscount/${itemId}`);
  }
}
