import { Component, OnInit, AfterViewInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { ProductsService } from '../products.service';
import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-edit-product',
  templateUrl: './edit-product.component.html',
  styleUrls: ['./edit-product.component.scss'],
})
export class EditProductComponent implements OnInit, AfterViewInit {
  image;
  productId;
  constructor(
    private fb: FormBuilder,
    private route: ActivatedRoute,
    private productsService: ProductsService,
    private toastr: ToastrService,
    private router: Router
  ) {}

  form = this.fb.group({
    image: [],
    name: ['', Validators.required],
    description: ['', Validators.required],
    price: ['', Validators.required],
    qty: ['', Validators.required],
    category: this.fb.group({
      _id: [],
      name: this.fb.group({
        en: [],
        ar: [],
      }),
    }),
    productAttributes: this.fb.array([]),
  });

  ngOnInit(): void {
    this.productId = this.route.snapshot.paramMap.get('id');
  }
  ngAfterViewInit() {
    this.getOne(this.productId);
  }

  getOne(id) {
    this.productsService.getOne(id).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.image = res['data']['image'];
        this.form.get('image').patchValue(res['data']['image']);
        this.form.get('name').patchValue(res['data']['name']);
        this.form.get('description').patchValue(res['data']['description']);
        this.form.get('price').patchValue(res['data']['price']);
        this.form.get('qty').patchValue(res['data']['qty']);
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }

  onSubmit(formValue) {
    const formData = new FormData();

    for (const key in formValue) {
      if (formValue[key] instanceof File) {
        formData.append(key, formValue[key]);
      } else {
        // contains object
        formData.append(key, JSON.stringify(formValue[key]));
      }
    }

    this.productsService.updateOne(formData, this.productId).subscribe(
      (res) => {
        console.log(res);
        this.image = null;
        this.form.reset();
        this.toastr.info(res['message'], 'Info');
        this.router.navigate(['/products']);
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }
}
