import { Component, OnInit, TemplateRef } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ToastrService } from 'ngx-toastr';
import { DataTableResource } from 'angular7-data-table';

import { ProductsService } from './products.service';

@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: ['./products.component.scss'],
})
export class ProductsComponent implements OnInit {
  itemResource;
  items = [];
  itemCount = 0;
  limit = 20;
  limits = [20, 25, 50];
  offset = 1;
  tableElements = [];

  modalRef: any;

  constructor(
    private productsService: ProductsService,
    private modalService: BsModalService,
    private toastr: ToastrService
  ) {}

  ngOnInit(): void {
    this.productsService.getAll();

    this.productsService.products.subscribe(async (data) => {
      if (data) {
        this.items = data;
        this.itemResource = new DataTableResource(data);
        this.itemCount = await this.itemResource.count();
        this.reloadItems({
          offset: 0,
          limit: this.limit,
        });
      }
    });
  }

  async reloadItems(event) {
    if (this.itemResource) {
      this.tableElements = await this.itemResource.query(event);
    }
  }

  async FilterRecords(FilterRecord) {
    this.tableElements = this.items;
    if (FilterRecord !== '') {
      this.tableElements = this.items.filter(
        (item) =>
          item.name.toLowerCase().includes(FilterRecord) ||
          item.category.en.toLowerCase().includes(FilterRecord) ||
          item.creator.name.toLowerCase().includes(FilterRecord) ||
          item.store?.name.toLowerCase().includes(FilterRecord)
      );
    }

    this.itemResource = new DataTableResource(this.tableElements);
    this.itemCount = await this.itemResource.count();
    this.reloadItems({
      offset: 0,
      limit: this.limit,
    });
  }

  itemId;
  openModal(template: TemplateRef<any>, id) {
    this.itemId = id;
    this.modalRef = this.modalService.show(template);
  }

  addDiscount(form) {
    console.log(form);
    this.modalRef.hide();
    this.productsService.addDiscount(form, this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.productsService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }

  deleteDiscount() {
    this.modalRef.hide();
    this.productsService.deleteDiscount(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.productsService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }

  deleteElement() {
    this.modalRef.hide();
    this.productsService.deleteOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.productsService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }

  activateElement() {
    this.modalRef.hide();
    this.productsService.activateOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.productsService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }

  deactivateElement() {
    this.modalRef.hide();
    this.productsService.deactivateOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.productsService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }

  soldElement() {
    this.modalRef.hide();
    this.productsService.soldOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.productsService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }

  availableElement() {
    this.modalRef.hide();
    this.productsService.availableOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.productsService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }
}
